import { HFormat, HIs, MyUI } from "@macashipo/mlib"
import React from "react"
import { KMore } from "src/helpers/Flows/TableCell"
import { KMyUITypeCommon } from "src/helpers/K"
import useConfigTableCell from "../../hooks/useConfigTableCell"
export default function MTCReadonlyHtml(props) {
  const { cell } = props
  const ctc = useConfigTableCell({ props })
  if (HIs.isObject(cell)) {
    return <div></div>
  }
  let _configFormat = {}
  if (ctc.more && ctc.more[KMore.configFormat]) {
    _configFormat = ctc.more[KMore.configFormat]
  }
  return (
    <div className="mct-readonly_percent disabled" style={ctc.styleWrap()}>
      {HFormat.format(cell, {
        type: "number_percent",
        ..._configFormat,
      })}
    </div>
  )
}
